.card__content {
	position: relative;
	overflow: hidden;
	padding: 1rem;

	.floating-icons {
		position: absolute;
		inset: 0;
		pointer-events: none;
		overflow: hidden;

		.icon {
			position: absolute;
			width: 60px;
			height: 60px;
			opacity: 0;
			transition: opacity 0.3s ease;

			svg {
				width: 100%;
				height: 100%;
				fill: currentColor;
				color: #4f8cc9;
			}
		}
	}

	// show icons only on hover
	&:hover {
		.floating-icons {
			.icon {
				animation: floatAround 12s infinite ease-in-out alternate;
				opacity: 0.5;

				// "randomness" using nth-child
				&:nth-child(1) {
					top: 10%;
					left: 20%;
					animation-delay: 0s;
					animation-duration: 14s;
				}
				&:nth-child(2) {
					top: 30%;
					left: 70%;
					animation-delay: 2s;
					animation-duration: 12s;
				}
				&:nth-child(3) {
					top: 80%;
					left: 40%;
					animation-delay: 4s;
					animation-duration: 16s;
				}
				&:nth-child(4) {
					top: 50%;
					left: 10%;
					animation-delay: 1s;
					animation-duration: 18s;
				}
				&:nth-child(5) {
					top: 60%;
					left: 80%;
					animation-delay: 3s;
					animation-duration: 15s;
				}
				&:nth-child(6) {
					top: 20%;
					left: 50%;
					animation-delay: 5s;
					animation-duration: 20s;
				}
				&:nth-child(7) {
					top: 70%;
					left: 60%;
					animation-delay: 6s;
					animation-duration: 13s;
				}
			}
		}
	}
}

// KEYFRAME that moves in both X and Y directions
@keyframes floatAround {
	0% {
		transform: translate(0, 0) rotate(0deg);
	}
	25% {
		transform: translate(80px, -60px) rotate(90deg);
	}
	50% {
		transform: translate(-60px, 100px) rotate(180deg);
	}
	75% {
		transform: translate(100px, 40px) rotate(270deg);
	}
	100% {
		transform: translate(0, 0) rotate(360deg);
	}
}
